<script>

    export default {
        name: 'App',
        components: {},
        mounted() {
           console.log(this)
        },
        methods: {
            forward(){
                this.$router.forward()
            },
            back(){
                this.$router.back()
            },
            back3(){
                this.$router.go(-3)
            },
            forward3(){
                this.$router.go(3)
            },
        },
    }
</script>

