<template>
    <div>
        <h1>
            我是消息
        </h1>
        
        <ul>
            <li v-for='news in newss' :key='news.id'>{{news.name}}</li><input />
        </ul>
       <div :style='{opacity}'>
           it is news
       </div>
    </div>
</template>

<script>
    export default {
        name: 'News',
        data() {
            return {
                newss: [
                    { id:'11', name: '今天好样的'},
                    { id:'22', name: '收复台湾'},
                    { id:'33', name: '解放全球'}
                ],
                opacity: 1
            }
        },
        activated() {
             this.timer = setInterval(()=>{
                 console.log('$$')
                this.opacity -= 0.1;
                if( this.opacity <= 0){
                    this.opacity = 1;
                }
            },16)
        },
         deactivated() {
            clearInterval(this.timer)
        },
        // 通过路由手段进来的，也可以组件内部这样
        beforeRouteEnter (to, from, next) {
            // ...
            console.log('组件enter')
            next()
        },
         // 通过路由手段进来的，也可以组件内部这样
        beforeRouteLeave (to, from, next) {
            // ...
            console.log('组件level')
            next()
        }
    }
</script>

