<script>
    export default {
        name: 'Msg',
        data() {
            return {
                msgs: [
                    { id:'11', name: '今天赚钱了'},
                    { id:'22', name: '今天洗澡了'},
                    { id:'33', name: '今天看电影了'}
                ]
            }
        },
        methods: {
            replace(msg){
               this.$router.replace({
                    name: 'detail',
                    query: {
                        id: msg.id,
                        name: msg.name
                    },
                })
            },
            push(msg){
                this.$router.push({
                    name: 'detail',
                    query: {
                        id: msg.id,
                        name: msg.name
                    },
                })
            }
        },
    }
