import VueRouter from 'vue-router'
import Home from '../pages/Home.vue'
import About from '../pages/About.vue'
import User from '../pages/User.vue'
import Message from '../pages/Message.vue'
import News from '../pages/News.vue'
import Mm from '../pages/Mm.vue'
import Detail from '../pages/Detail.vue'


const router =  new VueRouter({
    // mode: 'hash',
    mode: 'history',
    routes: [
        {
            path: '/',
            component: Home,
            // 存放额外的信息
            meta:{ title: '首页'}
        },
        {
            path: '/about',
            component: About,
            name: 'about',   // 也可以取name 当路由很深的时候
            meta:{ title: '关于'},
            children: [
                {    
                    // 不需要斜杠
                    path: 'news',
                    component: News,
                    meta:{ title: '新闻', needAuthor: true},

                },
                {    
                    // 不需要斜杠
                    path: 'mm',
                    component: Mm,
                    meta:{ title: 'mm', needAuthor: true},
                },
                {    
                    // 不需要斜杠
                    path: 'message',
                    component: Message,
                    meta:{ title: '信息'},
                    beforeEnter(to, from, next){
                        console.log('单独首位enter')
                        if(localStorage.getItem('user') !== 'wcl'){
                            alert('你没有权限访问')
                        }else{
                            next()
                        }
                    },
                    children: [
                        // query形式
                        {
                            path: 'detail',
                            component: Detail,
                            name: 'detail',
                            meta:{ title: '细节'},
                            props: ({ query }) => {
                                return {...query}
                            }
                        },
                        // params形式
                        // {   
                        //     path: 'detail/:id/:name',
                        //     name: 'detail',
                        //     component: Detail,
                        //     // props 属性
                        //     // props: { a: 1, b: 2},
                        //   若 props 属性 为 true, 会将params直接变成 props传入
                        //     props: true
                        // },
                    ]
                }
            ]
        },
        {
            path: '/user',
            component: User,
            meta:{ title: '用户'},
        },
   ]
})

// next执行才会往下走
// 执行是在 初始化 和路由变化
router.beforeEach((to,from, next)=>{
    // 可以在meta里面加上需要鉴权的  也可以根据from和to的： name 和 path等判断
    console.log("全局路由before")
    if(to.meta.needAuthor){
        // 鉴权规则一般是在
        if(localStorage.getItem('user') !== 'wcl'){
            alert('你没有权限访问')
        }else{
            next()
        }
    }else{
        next()
    }
})

// 执行是在 初始化 和路由变化
router.afterEach((to, from)=>{
   // 这个全局首位没有next，是拦截不住的。主要是改变title 
   console.log("全局路由after")
   document.title = to.meta.title
})

export default router